import React from 'react'
import Nav from '../components/common/nav'
import Footer from '../components/common/footer'
import Head from 'next/head'
import {Collapse} from 'antd'
const {Panel} = Collapse;
export default () => {
    return (
        <div className="bg-gray-100" style={{backgroundImage: "url('/static/img/bg.svg')"}}>
            <Head>
                <title>CryptoFair - The easiest and fastest way to make money in crypto industry</title>
            </Head>
            <div className="bg-white">
                <div className="container mx-auto px-4">
                    <Nav />
                </div>
            </div>
            <div className="container mx-auto px-4 py-32">
                <div className="mb-4">
                    <p className="text-4xl text-cryp-text font-bold text-center mb-8">Ethereum FAQ</p>
                    <div className="flex md:-mx-4 flex-wrap">
                        <div className="w-full md:w-1/3 mb-3 md:mb-0 md:px-4 text-center">
                            <a href="/faqethmm" className="hover:text-cryp-bold">
                                <img src="/static/img/i_metamask_icon.svg" className="w-24 h-24 mx-auto"/>
                                <p className="text-xl mt-2 mb-0">MetaMask FAQ</p>
                            </a>                            
                            <p className="text-cryp-span">plugin</p>
                        </div>
                        <div className="w-full md:w-1/3 mb-3 md:mb-0 md:px-4 text-center">
                            <a href="/faqethtw" className="hover:text-cryp-bold">
                                <img src="/static/img/i_trustwallet_icon.svg" className="w-24 h-24 mx-auto"/>
                                <p className="text-xl mt-2 mb-0">TrustWallet FAQ</p>
                            </a>                            
                            <p className="text-cryp-span">mobile application</p>
                        </div>
                        <div className="w-full md:w-1/3 mb-3 md:mb-0 md:px-4 text-center">
                            <a href="/faqethmew" className="hover:text-cryp-bold">
                                <img src="/static/img/i_myetherwallet_icon.svg" className="w-24 h-24 mx-auto"/>
                                <p className="text-xl mt-2 mb-0">MyEtherWallet FAQ</p>
                            </a>                            
                            <p className="text-cryp-span">web version</p>
                        </div>
                    </div>
                </div>
                <div className="w-full shadow bg-white px-4 py-16">
                    <p className="text-2xl text-cryp-text">General questions</p>
                    <Collapse bordered={false} className="">
                        <Panel header="What is Ethereum and why exactly it is used in the system?" className="text-lg">
                            <p className="">Ethereum - it is the second cryptocurrency after Bitcoin by popularity, and the first, by popularity, in smart contracts.</p>
                            <p className="">Features of the project on the Ethereum smart contract:</p>
                            <p className="">1) Your Ethereum wallet can't be blocked or hached even theoretically.</p>
                            <p className="">2) CryptoFair system - a code, uploaded to the main Ethereum network. This can't be deleted or changed in that network. It means that Your account and CryptoFair system aswell can't be hacked or blocked even in theory.</p>
                            <p className="">3) The code is completely open, and any programmer in the world can make sure that the algorithm works this way and not otherwise.</p>
                            <p className="">4) All transactions are public and accepts automatically. Funds DIRECTLY go to Your Wallet, so you don't need to confirm or request payments. The human factor is excluded completely.</p>
                            <p className="">5) Even if the CryptoFair site ceases to exist and the administration disappears, the CryptoFair smart contract in the Ethereum network will continue to exist, and it means that the System itself will continue its work. Even in this case all the accounts and payments will continue to work properly. Any programmer in the world can make a site mirror of the same smart contract as the entire system, as well as a specific group.</p>
                            <p className="">6) You can invite referrals without a website at all. To register a referral you must send to the address of the smart contract 0.05 ETH and in the field "Input DATA " specify the address of your upline.</p>
                            <p className="">7) Ethereum cryptocurrencie allows to use Repeat function, saving decentralization and transfers directly between the parties through the smart contract.</p>
                        </Panel>
                        <Panel header="How to create Ethereum wallet and to start using it?" className="text-lg">
                            <p className="">Ethereum wallet - is, first of all, two things:</p>
                            <p className="">1) Your public key - it is exactly Your Ethereum wallet number. No need to hide it.</p>
                            <p className="">2) Your private key - an analog of the usual password. It is your personal. The loss of a private key means the loss of the Wallet's control. If someone seized your private key, he seized your money, and you will not be able to change it for another private key.</p>
                            <p className="text-red-400">We never ask you private keys</p>
                            <p className="">There are several Ethereum wallets. The most popular and simimple in use and features of each of them are written below.</p>
                            <div className="w-full flex flex-wrap md:mx-4">
                                <div className="w-full md:w-1/2 md:px-4 mb-4">
                                    <p className="text-center font-bold">MetaMask</p>
                                    <div className="">
                                        <p className="">Full integration with CryptoFair. Registration and all further purchases of levels in 1 click, all additional fields are filled automatically.</p>
                                        <p className="">Allows you to export and import private keys - means that You remain full control over your Ethereum wallet.</p>
                                        <p className="">Convenient and easy to use. It is the most popular and secure wallet plugin for the browser. You can deactivate it or delete it from the browser at any time, with all access details left with you.</p>
                                    </div>
                                </div>
                                <div className="w-full md:w-1/2 md:px-4 mb-4">
                                    <p className="text-center font-bold">Trust Wallet</p>
                                    <div className="">
                                        <p className="">Full integration with CryptoFair through the built-in DApps browser.</p>
                                        <p className="">Paste referral link to the built-in DApps browser and registration with all subsequent level purchases will be in 1 click.</p>
                                        <p className="">Allows to import private keys. If you already have Ethereum wallet, You can also use it with the mobile application.</p>
                                        <p className="">The application is easy to use and has an intuitive interface.</p>
                                    </div>
                                </div>
                                <div className="w-full md:w-1/2 md:px-4 mb-4">
                                    <p className="text-center font-bold">MyEtherWallet</p>
                                    <div className="">
                                        <p className="">No need to install plugins and apps. All the transactions performs directly from the MyEtherWallet site.</p>
                                        <p className="">All the general and additional fields fills manually. For advanced useres only.</p>
                                    </div>
                                </div>
                            </div>
                            <p className="">Impornatnt! If you have registered Ethereum wallet on any service, You can always continue using with another one.
For example, if you registered Ethereum through MetaMask, you can continue to use the SAME wallet through MyEtherWallet and contrariwise.
All that You need - Your private key.</p>
                        </Panel>
                        <Panel header="How to fill up Your Ethereum wallet?" className="text-lg">
                            <p className="">Through exchange service (recommended):</p>                            
                            <p className="">1) Go to the site <a href="https://bestchange.com" className="" target="_blank">bestchange.com</a>. This is a monitoring of proven exchangers with the most favorable exchange rates of the most popular payment systems and banks of most countries.</p>
                            <p className="">2) Select the appropriate payment system via which service you want to make an exchange for Ethereum.</p>
                            <p className="">3) Choose the right exchanger with the best rate..</p>
                            <p className="">4) Go to the site of the selected exchanger and follow the simple instructions. The exchange is usually automatic and confirms instantly.</p>
                            <p className="">5) Make sure that funds have been received in your Ethereum wallet.</p>
                            <p className="">Notes:</p>
                            <p className="">When paying from bank cards, exchangers can request card verification for the first time. Usually it is a photo of the card on the exchange page background. Subsequent payments will be instant..</p>
                            <p className="">If you do not want to verify your card on the exchanger's site, you can first top up the account of another payment system instantly from your bank card (NOT through the exchanger, but in the payment system itself), and then send this funds to the Ethereum wallet with another payment system without verification.</p>
                            <p className="">Through LocalEthereum service:</p>
                            <p className="">1) Go to the <a href="https://localethereum.com" className="" target="_blank">localethereum.com</a>. This is the service of buying and selling Ethereum through any payment system.</p>
                            <p className="">2) Sign up. It is fast.</p>
                            <p className="">3) Choose the appropriate exchange for your country.</p>
                            <p className="">4) Follow the simple service instructions.</p>
                            <p className="">5) Make sure that funds have been received in your Ethereum wallet.</p>
                        </Panel>
                        <Panel header="How to withdraw funds from Ethereum wallet to other payment systems?" className="text-lg">
                        <p className="">Withdrawal of funds from Ethereum is the same as its top up, only the opposite direction of exchange should be chosen</p>
                        <p className="">Note. The service <a href="https://bestchange.com" className="" target="_blank">bestchange.com</a> is well suited for withdrawing any amounts to bank cards, including instantly and automatically for popular banks.</p>
                        </Panel>
                        <Panel header="What is 'Gas', 'Gas price' (Gwei) and 'Gas limit'?" className="text-lg">
                        <p className=""><span className="font-bold">Gas (Gas)</span> — This is the unit of calculation that is used to calculate and pay the commission for a specific action or transaction.</p>
                        <p className=""><span className="font-bold">Gas price (Gwei)</span> — It is the price, which miners have to pay for calculations. The basic principle remains the same - paid more - the transaction will be faster. The current gas price can be found here: <a href="https://etherscan.io/gastracker" className="" target="_blank">https://etherscan.io/gastracker</a>.</p>
                        <p className="">If you put a small gas price, then the transaction will be passing until the load on the Ethereum network decreas. This can take up to several hours.</p>
                        <p className=""><span className="font-bold">Gas Limit</span>— the maximum amount of Gas that you can pay during a transaction. The recommended value of Gas Limit = 400000. In some applications, the default value of Gas Limit = 1,000,000. This does NOT mean that you will be charged more per transaction. The Charge in any case the same and unspent funds will return to the balance.</p>
                        <p className="">If you put a small limit of Gas then the transaction will not pass and the funds will return to the balance, but part of the commission in this case will still be charged.</p>
                        </Panel>
                        <Panel header="Why is the transaction pending?" className="text-lg">
                            <p className="">The most likely reason - the low price of gas (Gwei). The current price of gas can be found here: <a href="https://etherscan.io/gastracker" className="" target="_blank">https://etherscan.io/gastracker</a></p>
                        </Panel>
                        <Panel header="Why is the transaction rejected?" className="text-lg">
                            <p className="">The most likely cause is a small value of the gas limit. We recommend setting a gas limit of 400,000.</p>
                        </Panel>
                        <Panel header="Why when sending it is written 'insufficient funds', if the funds are enough with a margin?" className="text-lg">
                            <p className="">Some ethereum wallets, for example, Trust Wallet, sets the Gas Limit to 1000000 by default. Remind that this does NOT mean that you will be charged more, this means that there should be a reserve on the wallet.Try to reduce this amount to 400000.</p>
                            <p className="">If after all it is still written that there are not enough funds, then, perhaps, the Ethereum network is loaded more than usual, for example, 20 Gwei or 40 Gwei (the normal value is 5 Gwei). In this case, you can wait until the load on the network decreas. You can reduce (FOR A LITTLE) the price of gas by yourself, but then the transaction will take longer in time up to several hours instead of several seconds. Also You can add some funds to the wallet, so that was enough to make a transaction.</p>
                            <p className="">Please note that the estimated commission that was written on the transaction is NOT the actual commission, but its maximum possible value if the entire Gas Limit is used up. The real transaction fee will be much less.</p>
                        </Panel>
                    </Collapse>
                </div>
            </div>
            <Footer />
        </div>
    )
}